.attachment-cmp {
    display: flex;
    flex-direction: column;



    & .upload-source {
        margin-bottom: 7px;
        cursor: pointer;

        &:hover {
            background-color: #091e4214;
        }

        & .input-computer-upload {
            position: absolute;
            opacity: 0;
            cursor: pointer;

            &:hover {
                background-color: #091e4214;
                background-color: #485161;
                cursor: pointer;
            }
        }
    }

    & .upload-input {

        & label {
            @include for-h3-dynamic-cmp
        }

        & input {
            @include for-dynamic-input
        }
    }

    & button {
        background-color: #091e420a;
        border: none;
        box-shadow: none;
        cursor: pointer;
        display: inline-block;
        font-weight: 400;
        line-height: 20px;
        margin: 8px 4px 0 0;
        padding: 6px 12px;
        text-align: center;
        color: #485161;

        &:hover {
            color: #202c41;
            background-color: #0919340a;

        }
    }
}