.board-app {
  height: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;

  & .board-header {
    padding: 4px 4px 4px 0;
    width: 100vw;
    display: flex;
    justify-content: space-between;

    & .nav-left {
      padding-left: 20px;

      & h1 {
        border-radius: 3px;
        color: #fff;
        cursor: text;
        font-family: Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans,
          Helvetica Neue, sans-serif;
        font-size: 18px;
        font-weight: 700;
        height: 30px;
        margin: 0;
        min-width: 30px;
        padding: 7px 5px;
        text-overflow: ellipsis;
        background-color: hsla(0, 4%, 82%, 0.239);
      }
    }
  }
}