.dynamic-cmp {
    background-color: #fff;
    border: 1px solid #d3d3d3;
    border-radius: 3px;
    box-shadow: 0 8px 16px (9, 30, 66), 0 0 0 1px rgba(9, 30, 66, .08);
    min-height: 80px;
    position:absolute;
    width: 100%;
    // height: 100%;
    // top: 20px !important;
    margin-left: -53px;
    @include for-normal-layout{
        margin-left: 0px;
        left: auto;
        min-height: 50px;
        width: 321px;
        position: fixed;
            top: auto ;
            overflow-y: auto;
            overflow-x: hidden;
        
    }

    z-index: 35;
    &-content {
        padding: 12px;
    }

    &-header {
        align-items: center;
        border-bottom: 1px solid #091e4221;
        color: #5e6c84;
        display: flex;
        justify-content: center;
        line-height: 40px;
        margin: 0 12px;
        padding: 0 32px;
    }

    &-close {
        background-color: unset;
        border: none;
        font-size: 12px;
        padding: 10px 0 10px 8px;
        position: absolute;
        right: 12px;
        top: 5px;
    }
}